/* Normalize -------------------------------------------------------------------- */
*,
:after,
:before {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote,
pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset,
form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas,
details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section,
summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  font-family: 'Open Sans', sans-serif;
  background-color: '#fff';
  height: 100%;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: none;
  min-width: 320px;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  max-width: 100%;
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  outline: none;
  border-radius: 0;
  -webkit-appearance: none;
}

button,
input {
  overflow: visible;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

ol, ul {
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

textarea {
  resize: vertical;
  overflow: auto;
}

::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  overflow: visible;
}

button,
select {
  text-transform: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

select::-ms-expand {
  display: none;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

.placeholder {
  line-height: normal;
}

:-moz-placeholder {
  line-height: normal;
}

::-moz-placeholder {
  line-height: normal;
}

:-ms-input-placeholder {
  line-height: normal;
}

::-webkit-input-placeholder {
  line-height: normal;
}

/* Fonts -------------------------------------------------------------------- */
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans.eot");
  src: url("../fonts/OpenSans.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSans.woff2") format("woff2"), url("../fonts/OpenSans.woff") format("woff"), url("../fonts/OpenSans.ttf") format("truetype"), url("../fonts/OpenSans.svg#OpenSans") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* Classes -------------------------------------------------------------------- */
.container {
  width: 100%;
  padding: 0 15px;
  max-width: 1366px;
  margin: 0 auto;
}

/* Styles -------------------------------------------------------------------- */
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

.header__inner {
  padding: 20px 0;
  display: flex;
  align-items: center;
}

.header__nav {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.header__nav-item {
  display: inline-block;
  color: #ffffff;
  transition: all .2s ease-out;
}

.header__nav-item:hover {
  opacity: .7;
}

.header__nav > * + * {
  margin-left: 55px;
}

.footer__top {
  padding: 70px 0 90px;
  display: flex;
}

.footer__card {
  width: 20%;
}

.footer__card--long {
  width: 40%;
}

.footer__card-title {
  font-size: 18px;
  font-weight: 700;
}

.footer__card-list {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footer__card-list-item {
  display: inline-block;
  transition: all .2s ease-out;
}

.footer__card-list-item:hover {
  opacity: .7;
}

.footer__card-list-item + .footer__card-list-item {
  margin-top: 25px;
}

.footer__social-nav {
  display: flex;
  align-items: center;
  margin-top: 35px;
}

.footer__social-item {
  transition: all .2s ease-out;
  padding: 5px;
}

.footer__social-item img {
  width: 20px;
}

.footer__social-item:hover {
  opacity: .7;
}

.footer__social-item + .footer__social-item {
  margin-left: 20px;
}

.footer__bottom {
  border-top: 1px solid #e1ecff;
  padding: 30px 0;
}

.footer__bottom-content {
  text-align: center;
  font-size: 12px;
  color: #353535;
}

.logo {
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
}

.logo__icon {
  width: 35px;
}

.logo__title {
  color: #ffffff;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin-left: 10px;
}

.logo__title--black {
  color: #000;
}

.btn {
  display: inline-block;
  text-transform: uppercase;
  color: #ffffff;
  padding: 13px 30px;
  transition: all .2s ease-out;
  border-radius: 4px;
  font-weight: 700;
}

.btn:hover {
  opacity: .7;
}

.btn--light {
  background-color: rgba(153, 211, 255, 0.5);
  border: 1px solid #8db5fa;
}

.btn--grad {
  background-image: linear-gradient(to right, #f8b28c 0%, #d64562 100%);
  padding: 15px 50px;
}

.main {
  min-height: 95vh;
  background-image: url("../img/bg_pattern@4x.png");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
  display: flex;
  align-items: center;
}

.main__scroll-icon {
  position: absolute;
  bottom: 0;
  right: 65px;
  width: 22px;
}

.main__inner {
  padding: 100px 0;
  text-align: center;
  color: #ffffff;
}

.main__inner .btn {
  margin-top: 55px;
}

.main__title {
  font-size: 48px;
  font-weight: 700;
}

.main__title span {
  color: #da5467;
}

.main__caption {
  font-size: 22px;
  margin-top: 45px;
}

.main__desc {
  font-size: 16px;
  max-width: 600px;
  margin: 45px auto 0;
  line-height: 25px;
}

.we-do {
  padding: 100px 0 70px;
}

.we-do__tabs {
  display: flex;
  align-items: center;
  margin-top: 85px;
}

.we-do__tabs-nav {
  width: 50%;
  counter-reset: list;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.we-do__tabs-nav-item {
  font-size: 28px;
  font-weight: 700;
  color: #000;
  opacity: .5;
  cursor: pointer;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  transition: all .2s ease-out;
}

.we-do__tabs-nav-item::before {
  content: "0" counter(list);
  counter-increment: list;
  color: #d84c64;
  margin-right: 15px;
}

.we-do__tabs-nav-item--active {
  opacity: 1;
}

.we-do__tabs-nav-item:hover {
  margin-left: 10px;
}

.we-do__tabs-nav-item + .we-do__tabs-nav-item {
  margin-top: 45px;
}

.we-do__tabs-content {
  width: 50%;
}

.we-do__tabs-content-title {
  margin-top: 25px;
  font-size: 19px;
  font-weight: 700;
}

.we-do__tabs-content .text {
  margin-top: 15px;
}

.title {
  color: #000000;
  font-size: 28px;
  font-weight: 700;
}

.title--center {
  text-align: center;
}

.title--white {
  color: #ffffff;
}

.billet {
  padding: 70px 0;
}

.billet__inner {
  width: 100%;
  max-width: 80%;
  box-shadow: 0 7px 17px rgba(68, 128, 235, 0.14);
  border-radius: 4px;
  background-color: #ffffff;
  padding: 60px 70px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.billet__action {
  flex-shrink: 0;
  margin-left: 50px;
}

.billet .text {
  margin-top: 25px;
}

.text {
  font-size: 16px;
  color: #66707a;
  line-height: 25px;
}

.text--white {
  color: #ffffff;
}

.blog {
  background-color: #447fec;
  padding: 70px 0 100px;
}

.blog__about {
  margin-top: 35px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.blog__about .text {
  max-width: 800px;
}

.blog__about .btn {
  margin-left: 20px;
  flex-shrink: 0;
}

.blog__cards {
  margin-top: 45px;
  display: flex;
  justify-content: space-between;
}

.blog__cards-item {
  box-shadow: 0 7px 17px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  background-color: #fafafa;
  width: calc((100% / 3) - 25px);
  overflow: hidden;
  transition: all .2s ease-out;
}

.blog__cards-item:hover {
  box-shadow: 0 7px 17px rgba(0, 0, 0, 0.3);
}

.blog__cards-photo {
  height: 300px;
}

.blog__cards-photo img {
  object-position: center;
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.blog__cards-content {
  padding: 25px 30px 35px;
  display: flex;
  flex-direction: column;
}

.blog__cards-title {
  font-size: 17px;
  color: #000;
  font-weight: 700;
}

.blog__cards-desc {
  color: #66707a;
  font-size: 12px;
  margin-top: 15px;
  line-height: 17px;
}

.blog__cards-more {
  font-size: 12px;
  color: #3d7cdd;
  font-weight: 700;
  transition: all .2s ease-out;
  margin-top: 35px;
  display: inline-block;
  margin-left: auto;
}

.blog__cards-more:hover {
  color: #e1686f;
}

.post {
  padding: 70px 0;
}

.post__inner {
  display: flex;
  justify-content: space-between;
}

.post__photo {
  box-shadow: 0 7px 17px rgba(68, 128, 235, 0.14);
  position: relative;
  width: calc(50% - 25px);
  padding-bottom: calc(50% - 25px);
}

.post__photo img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.post__text {
  box-shadow: 0 7px 17px rgba(68, 128, 235, 0.14);
  width: calc(50% - 25px);
  padding: 50px;
  font-size: 25px;
  color: #66707a;
  line-height: 40px;
  letter-spacing: 0.21px;
}

.action {
  padding: 90px 0;
  background-image: linear-gradient(to right, #3d7ee0 0%, #3d7dde 100%);
  text-align: center;
}

.action .btn {
  margin-top: 75px;
}

/* Media -------------------------------------------------------------------- */
@media (max-width: 1199px) {
  .post__text {
    font-size: 20px;
    line-height: 32px;
  }
}

@media (max-width: 991px) {
  .main__scroll-icon {
    display: none;
  }
  .we-do {
    padding: 50px 0;
  }
  .we-do__tabs {
    margin-top: 50px;
  }
  .we-do__tabs-nav-item {
    font-size: 20px;
  }
  .we-do__tabs-nav-item + .we-do__tabs-nav-item {
    margin-top: 30px;
  }
  .header__nav > * + * {
    margin-left: 30px;
  }
  .billet {
    padding: 50px 0;
  }
  .billet__inner {
    max-width: 100%;
    padding: 50px;
  }
  .blog {
    padding: 50px 0;
  }
  .blog__cards-item {
    width: calc((100% / 3) - 10px);
  }
  .blog__cards-photo {
    height: 200px;
  }
  .post {
    padding: 50px 0;
  }
  .post__text {
    font-size: 18px;
    line-height: 29px;
    padding: 30px;
  }
  .action {
    padding: 50px 0;
  }
  .action .btn {
    margin-top: 50px;
  }
  .footer__top {
    padding: 50px 0;
  }
  .footer__bottom {
    padding: 20px 0;
  }
}

@media (max-width: 767px) {
  .header__nav-item {
    font-size: 12px;
  }
  .header__nav > * + * {
    margin-left: 10px;
  }
  .btn {
    font-size: 12px;
    padding: 10px 20px;
  }
  .logo__title {
    font-size: 11px;
    margin-left: 5px;
  }
  .logo__icon {
    width: 25px;
  }
  .main {
    background-image: linear-gradient(90deg, #3d7ee0 0, #3d7dde);
    min-height: 100%;
  }
  .main__title {
    font-size: 32px;
  }
  .main__caption {
    font-size: 20px;
    margin-top: 25px;
  }
  .main__desc {
    font-size: 14px;
    margin: 30px auto 0;
  }
  .main__inner {
    padding: 100px 0 50px;
  }
  .main__inner .btn {
    margin-top: 30px;
  }
  .title {
    font-size: 23px;
  }
  .text {
    font-size: 14px;
  }
  .we-do__tabs {
    flex-direction: column;
  }
  .we-do__tabs-nav {
    width: 100%;
  }
  .we-do__tabs-content {
    width: 100%;
  }
  .we-do__tabs-content-item {
    margin-top: 50px;
  }
  .we-do__tabs-content-title {
    font-size: 17px;
  }
  .billet__inner {
    padding: 30px;
    flex-direction: column;
    text-align: center;
  }
  .billet__action {
    margin-left: 0;
    margin-top: 25px;
  }
  .blog__about {
    flex-direction: column;
  }
  .blog__about .btn {
    margin-left: 0;
    margin-top: 15px;
  }
  .blog__cards {
    flex-direction: column;
  }
  .blog__cards-item {
    width: 100%;
  }
  .blog__cards-item + .blog__cards-item {
    margin-top: 15px;
  }
  .blog__cards-photo {
    height: 300px;
  }
  .post__inner {
    flex-direction: column;
  }
  .post__photo {
    width: 100%;
    padding-bottom: 75%;
  }
  .post__text {
    width: 100%;
    margin-top: 15px;
  }
  .footer__top {
    flex-wrap: wrap;
    padding: 30px 0;
  }
  .footer__card {
    width: 33%;
    margin-top: 30px;
  }
  .footer__card--long {
    width: 100%;
    margin-top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .footer__card-title {
    font-size: 16px;
  }
  .footer__card-list {
    margin-top: 30px;
  }
  .footer__card-list-item + .footer__card-list-item {
    margin-top: 15px;
  }
  .footer__social-nav {
    margin-top: 0;
  }
}

@media (max-width: 479px) {
  .header__inner {
    flex-direction: column;
    padding: 10px 0;
  }
  .header__nav {
    margin-left: 0;
    margin-top: 5px;
  }
  .post__text {
    font-size: 14px;
    line-height: 24px;
    padding: 15px;
  }
  .action .btn {
    margin-top: 30px;
  }
  .footer__card {
    width: 100%;
  }
  .footer__card--long {
    flex-direction: column;
  }
  .footer__card-list {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer__card-title {
    text-align: center;
  }
  .footer__card + .footer__card {
    margin-top: 30px;
  }
  .footer__social-nav {
    margin-top: 10px;
  }
}
